  mounted () {
    this.login();
    this.home();
    this.center();
    this.timeFn();
    this.cancelLoading();
  },
  methods: {
    login(){
      var promise = {
        uuid:222
      };
      this.$get(this.HOST.login, promise)
        .then(res=>{
          this.Cookies.set('token',res.token);
      })
    },
     home(){
      var promise = {};
      this.$get(this.HOST.home, promise)
        .then(res=>{
          console.log(res);
          this.nber = res.list;
      })
